body {
  margin: 0px;
  display:flex;
  background-color: #5bceb1;

}

nav ul {
  display: grid;
  position: relative;
  top: 0px;
  padding:10px;
}

nav ul h1,h2 {
  padding-bottom:0px;
  color: yellow;
  font-size: 24px;
  font-family: 'Lobster', cursive;
}

.media-box {
  width: 100%;
  padding-top: 10px;
  background-color: black;
  color: white;
  position: relative;
  box-sizing: border-box;
  object-fit: contain;
}

.media-box .nome {
  font-weight: bold;
  position: absolute;
  width: calc(100% -5px);
  background: transparent;
  color: white;
  height: 20%;
  bottom: 0;
  display: flex;
  align-items: center;
  padding-left: 5px;
  object-fit: contain;
}


main {
  display: flex;

}

nav {

  width: 100%;
  position: absolute;
  top: 0;
  padding: 10px;
}

nav ::before {
  background: image("./spider-man-cabe√ßalho-pagina.jpg");
}

section {
  display: grid;
}

section.container {
  display: flex;
  flex-direction: column;
  margin-top: 200px;
}

footer {
  background: #2b0101;
  color: white;
  position: relative;
  bottom: 0;
  width: 100%;
  display: flex;
  padding: 0 10px;
}

.cabecalho-box {
  width: 100%; 
  margin: 0px;
  padding: 0px;
  font-size:20px;

}

li a {
  font-size: 64px;
  font-family: 'Lobster', cursive;
  color:rgb(247, 139, 17);
}
